<Page>
  <Navbar title="//get_unstuck" backLink="Back"/>
  <Card>
    <List form>
      <ListInput
          label="Name your problem"
          type="text"
          floatingLabel
          value={problemTitle}
          onInput={(e) => problemTitle = e.target.value}
      />
    </List>
    <List>
      <ListButton onClick={addProblem} color="blue">Get Unstuck</ListButton>
    </List>
  </Card>

  <Block>
    <p><strong>When you are trying to solve a problem, if your next step is obvious, then do it.</strong></p>
    <p><strong>Most often this is not the case. When that happens, take a step back and think what it is that you need to figure out, or do, or work through next.</strong></p>
    <p><strong>Write down a question about a narrow part of that. Make it as specific as you can.</strong></p>
  </Block>
</Page>

<script>
  import {
    f7,
    Navbar,
    NavLeft,
    NavTitle,
    Link,
    Toolbar,
    Block,
    BlockTitle,
    Page,
    List,
    ListInput,
    ListButton,
    Card,
    CardContent,
  } from 'framework7-svelte'

  import { onMount } from 'svelte'

  export let f7router

  let problemTitle = localStorage.getItem('problemTitle') || ''

  function addProblem () {
    if (!problemTitle.length) {
      f7.dialog.alert('Title cannot be empty.')
      return
    }

    localStorage.setItem('problemTitle', problemTitle)
    f7router.navigate('/mid-step/')
  }
</script>
