<ListItem swipeout>
  <span slot="header">
    <strong>You:</strong> {comment.value}
  </span>
  <SwipeoutActions left>
    <SwipeoutButton close color="red" onClick="{deleteComment}">Delete Comment</SwipeoutButton>
  </SwipeoutActions>
</ListItem>

<script>
  import { ListItem, SwipeoutActions, SwipeoutButton } from 'framework7-svelte'
  import Api from '../../services/Api'
  import { createEventDispatcher } from 'svelte'

  export let comment
  const dispatch = createEventDispatcher()

  async function deleteComment() {
    await Api.deleteComment(comment.id)

    dispatch('deleteComment', { id: comment.id });
  }
</script>
