<Page>
  <Navbar title="//get_unstuck" backLink="Back" />

  <Card>
    <List form>
      <ListInput
        label="Add todo step"
        type="text"
        floatingLabel
        value={stepTitle}
        onInput={(e) => stepTitle = e.target.value}
      />
    </List>
    <List>
      <ListButton onClick={addStep} color="blue">Add Step</ListButton>
    </List>
  </Card>

  <Block>
    <p><strong>What isn't obvious? What do you need to figure out.</strong></p>
    <p><strong>Create a todo step and ask a question. Make it as specific as you can.</strong></p>
  </Block>
</Page>

<script>
  import {
    f7,
    Navbar,
    List,
    ListButton,
    ListInput,
    Page,
    Card,
    CardContent,
    CardFooter,
    Button,
    Block,
  } from 'framework7-svelte';

  import Api from '../services/Api'

  export let f7router;
  export let f7route;
  let stepTitle = '';

  const problemId = f7route.params.problemId

  async function addStep () {
    if (!stepTitle.length) {
      f7.dialog.alert('Todo Step cannot be empty.')
      return
    }

    await Api.createQuestion(problemId, stepTitle)
    f7router.navigate('/')
  }
</script>
