<Card>
  <CardHeader  class="problemCard-header">
    <strong>{name}</strong>
  </CardHeader>
  <CardFooter class="problemCard-headerFooter">
    <div>{itemsLeft} Items Left</div>
    <div class="problemCard-toggle">
      <span>Public</span>
      <Toggle color="green" checked="{online === 'yes'}" on:toggleChange="{enableHelp}"/>
    </div>
  </CardFooter>

  {#if questions.length}
    <List class="problemCard-list">
      {#each questions as question}
        <Question question="{question}" on:toggleStep={handleMessage} />
      {/each}
    </List>
  {/if}

  <CardFooter class="problemCard-cardFooter">
    <Button popoverOpen=".{popoverClass}">Edit</Button>
    <div>
      <small><i>{Api.date(created_at)}</i></small>
    </div>
    <Button small onClick={() => f7router.navigate(`/add-question/${id}`)}>Add step</Button>
  </CardFooter>
</Card>

<Popover class="{popoverClass}">
  <List>
    <ListItem link="#" popoverClose title="Delete" on:click="{deleteProblem}" />
  </List>
</Popover>

<style>
  :global(.problemCard-list) {
    background: #d8f8ff;
  }
  :global(.problemCard-list li) {
    border-bottom : 1px solid #8ae7ef;
  }
  :global(.problemCard-headerFooter) {
    border-bottom : 1px solid #8ae7ef;
    border-radius: 0;
  }
  :global(.problemCard-cardFooter) {
  }
  :global(.problemCard-toggle span) {
    margin-right : 6px;
  }
</style>

<script>
  import { createEventDispatcher } from 'svelte'
  import { Card, CardHeader, CardFooter, Toggle, List, Button, Popover, ListItem, Icon } from 'framework7-svelte'
  import Question from '../components/Question.svelte'
  import Api from '../../services/Api'

  export let f7router
  export let problem

  const { id, name, online, created_at, questions } = problem
  const popoverClass = `popover-menu-${id}`
  const dispatch = createEventDispatcher()

  let itemsLeft = questions.filter(question => question.completed === 'no').length

  function enableHelp ({ detail }) {
    const flag = detail[0] ? 'yes' : 'no'
    Api.makeProblemPublic(id, flag)
  }

  function deleteProblem() {
    const conf = confirm('This question and all answers will be deleted.');

    if (conf === true) {
      Api.deleteProblem(id)
      dispatch('message', { id });
    }
  }

  function handleMessage (event) {
    const flag =  event.detail.flag === 'yes' ? 1 : -1;
    itemsLeft = itemsLeft - flag;
  }
</script>
