import axios from 'axios';
import { DateTime } from 'luxon';

class Api {
  constructor() {
    this.url = 'https://master.massinflux.com/get-shit-done';
    this.userId = localStorage.getItem('userId');
  }

  // eslint-disable-next-line class-methods-use-this
  date(val) {
    return DateTime.fromSQL(val).toFormat('LLLL dd, yyyy');
  }

  /**
   * @returns {Promise<*>}
   */
  async getResources() {
    const resp = await axios.get(`${this.url}/resources`);

    return resp.data;
  }

  /**
   * @param userId
   * @returns {Promise<*>}
   */
  async getProblems(userId) {
    const resp = await axios.get(`${this.url}/problems/${userId}`);

    return resp.data.data;
  }

  /**
   * @returns {Promise<*>}
   */
  async getPublicProblems() {
    const resp = await axios.get(`${this.url}/problems/public`);

    return resp.data.data;
  }

  /**
   * @param name
   * @returns {Promise<*>}
   */
  async createProblem(name) {
    const resp = await axios.post(`${this.url}/problem/create`, {
      user_id: this.userId,
      name,
    });

    return resp.data.data;
  }

  /**
   * @param id
   * @returns {Promise<void>}
   */
  async deleteProblem(id) {
    const resp = await axios.post(`${this.url}/problem/delete`, { id });

    return resp.data.data;
  }

  /**
   * @param id
   * @param flag
   * @returns {Promise<void>}
   */
  async makeProblemPublic(id, flag) {
    const resp = await axios.post(`${this.url}/problem/public`, {
      id,
      flag,
    });

    return resp.data.data;
  }


  /**
   * @param problemId
   * @param value
   * @returns {Promise<*>}
   */
  async createQuestion(problemId, value) {
    const resp = await axios.post(`${this.url}/question/create`, {
      problem_id: problemId,
      value,
    });

    return resp.data.data;
  }

  /**
   * @param questionId
   * @returns {Promise<*>}
   */
  async getAnswers(questionId) {
    const resp = await axios.get(`${this.url}/answers/${questionId}`);

    return resp.data.data;
  }

  /**
   * @param questionId
   * @param value
   * @returns {Promise<*>}
   */
  async createAnswer(questionId, value) {
    const resp = await axios.post(`${this.url}/answer/create`, {
      question_id: questionId,
      user_id: this.userId,
      value,
    });

    return resp.data.data;
  }

  /**
   * @param id
   * @returns {Promise<void>}
   */
  async deleteAnswer(id) {
    await axios.post(`${this.url}/answer/delete`, { id });
  }

  /**
   * @param id
   * @returns {Promise<void>}
   */
  async deleteQuestion(id) {
    await axios.post(`${this.url}/question/delete`, { id });
  }

  /**
   * @param id
   * @param flag
   * @returns {Promise<void>}
   */
  async completeQuestion(id, flag) {
    const resp = await axios.post(`${this.url}/question/complete`, {
      id,
      flag,
    });

    return resp.data.data;
  }

  /**
   * @param problemId
   * @returns {Promise<*>}
   */
  async getComments(problemId) {
    const resp = await axios.get(`${this.url}/comments/${problemId}`);
    return resp.data.data;
  }

  /**
   * @param problemId
   * @param value
   * @returns {Promise<*>}
   */
  async createComment(problemId, value) {
    const resp = await axios.post(`${this.url}/comment/create`, {
      problem_id: problemId,
      user_id: this.userId,
      value,
    });

    return resp.data.data;
  }

  /**
   * @param id
   * @returns {Promise<void>}
   */
  async deleteComment(id) {
    await axios.post(`${this.url}/comment/delete`, { id });
  }
}

export default new Api();
