<ListItem swipeout link="/add-answer/{question.id}" class="{itemClass}"
          title="{question.answers.length} Answers" onSwipeoutDeleted="{deleteStep}">
  <span slot="header">
    {question.value}
  </span>
  <span slot="after">
    Answer
  </span>
  <f7-icon slot="media" class="{itemIconClass}">
    <Icon f7="checkmark_circle"/>
  </f7-icon>
  <SwipeoutActions left>
    <SwipeoutButton close color={color} onClick="{toggleStep}">Mark {title}</SwipeoutButton>
  </SwipeoutActions>
  <SwipeoutActions right>
    <SwipeoutButton delete confirmText="Are you sure you want to delete this step?">Delete</SwipeoutButton>
  </SwipeoutActions>
</ListItem>

<script>
  import { ListItem, SwipeoutActions, SwipeoutButton, Icon } from 'framework7-svelte'
  import Api from '../../services/Api'
  import { createEventDispatcher } from 'svelte'

  export let question
  const dispatch = createEventDispatcher()

  $: color = question.completed === 'yes' ? 'deeporange' : 'green';
  $: title = question.completed === 'yes' ? 'Not Complete' : 'Complete';
  $: itemClass = question.completed === 'yes' ? 'step text-through' : 'step';
  $: itemIconClass = question.completed === 'yes' ? 'slot-icon slot-iconSuccess' : 'slot-icon';

  function deleteStep() {
    Api.deleteQuestion(question.id)
  }

  async function toggleStep() {
    const flag = question.completed === 'yes' ? 'no' : 'yes';
    question = await Api.completeQuestion(question.id, flag)

    dispatch('toggleStep', { flag });
  }
</script>
