<Page>
  <Navbar title="//get_unstuck" backLink="Back"/>

  <Card>
    <CardContent>Is your next step obvious?</CardContent>
    <CardFooter>
      <Button onClick={yes}>Yes</Button>
      <Button onClick={no}>No</Button>
    </CardFooter>
  </Card>

  <Block>
    <p><strong>Is your next step obvious?</strong></p>
  </Block>
</Page>

<script>
  import {
    f7,
    Navbar,
    Page,
    Card,
    CardContent,
    CardFooter,
    Button,
    Block,
  } from 'framework7-svelte'

  import { onMount } from 'svelte'
  import API from '../services/Api'

  export let f7router

  async function yes () {
    await createProblem()

    f7router.navigate('/')
  }

  async function no () {
    const { id } = await createProblem()

    f7router.navigate(`/add-question/${id}`)
  }

  async function createProblem () {
    f7.dialog.preloader()

    // create problem
    const problem = await API.createProblem(localStorage.getItem('problemTitle'))

    localStorage.removeItem('problemTitle')

    f7.dialog.close()

    return problem
  }
</script>
