<Page name="home">
  <Toolbar tabbar position="bottom">
    <Link href="/" tabLinkActive>Home</Link>
    <Link href="/public/">Public</Link>
  </Toolbar>

    {#if problems}
        {#if problems.length}
          <Block>
            <Row>
              <Col width="50">
                <strong>Solving the problem</strong>
              </Col>
              <Col width="50" class="textRight">
                <Link href="/resources/" style="margin-right: 10px">Resources</Link>
                <Link href="/about/">About</Link>
              </Col>
            </Row>
          </Block>
            {#each problems as problem, i (problem.id)}
              <Problem problem="{problem}" f7router="{f7router}" on:message={handleMessage}/>

                {#if i === 0}
                  <Block>
                    <div><strong>Tip:</strong> Swipe the Step for more actions</div>
                    <div><strong>Tip:</strong> Make problems public and others can comment and help</div>
                  </Block>
                {/if}
            {/each}
          <Fab position="center-bottom" text="Add" onClick={() => f7router.navigate('/add-problem/')}>
            <Icon ios="f7:plus" aurora="f7:plus" md="material:add"/>
          </Fab>
        {:else}
          <Block>
            <p><strong>Often you will have multiple questions in your mind about your problem, and can be difficult to focus.</strong></p>
            <p><strong>Having everything written down will help solve your focus, and given enough time, you will solve the problem.</strong></p>
            <br>
            <Button fill href="/add-problem/">Start by asking a question</Button>
          </Block>
        {/if}
    {/if}
</Page>

<script>
  import {
    f7,
    Navbar,
    NavLeft,
    NavTitle,
    NavTitleLarge,
    NavRight,
    Link,
    Toolbar,
    Block,
    BlockTitle,
    Page,
    List,
    Card,
    CardContent,
    CardFooter,
    Button,
    Fab,
    Icon,
    Row,
    Col,
  } from 'framework7-svelte'

  import Api from '../services/Api'
  import Problem from '../pages/components/Problem.svelte'

  export let f7router
  let problems = null
  const userId = localStorage.getItem('userId')

  if (!userId) {
    f7router.navigate('/login/')
  }

  function handleMessage (event) {
    let resp = problems.filter(problem => problem.id !== event.detail.id)
    problems = [...resp]
  }

  // initial data
  Api.getProblems(userId).then(resp => problems = resp)
</script>
