<Page noToolbar noNavbar noSwipeback loginScreen>
  <Card>
    <CardHeader>Get Unstuck</CardHeader>
    <CardContent>
      <List form>
        <ListInput
                label="Sign in with your phone number"
                type="number"
                placeholder="Enter your phone number"
                value={phoneNumber}
                onInput={(e) => phoneNumber = e.target.value}
        />
      </List>
      <List>
        <br>
        <ListButton onClick={signIn}>Sign In</ListButton>
      </List>
    </CardContent>
  </Card>
</Page>

<script>
  import {f7, Page, Card, CardContent,  CardHeader, LoginScreenTitle, List, ListInput, ListButton, BlockFooter} from 'framework7-svelte';
  import { onMount } from 'svelte';
  import axios from 'axios';
  import { options } from '../js/options';

  export let f7router;

  let phoneNumber = '';

  async function signIn() {
    if(phoneNumber.trim() === '') {
      f7.dialog.alert('Please enter a phone number.');
    } else if(phoneNumber.trim().length < 10) {
      f7.dialog.alert('Invalid phone number. Please try again.');
    } else {
      f7.dialog.preloader();
      const resp = await axios.post(`${options.requestUrl}/set-phone-number`, { phone: phoneNumber })
      f7.dialog.close();

      if(resp.data.type !== 'success') {
        f7.dialog.alert('There was an error. Please try again.');
      } else {
        localStorage.setItem('userId', resp.data.message);
        f7router.navigate('/');
      }
    }
  }

  onMount(() => {

  })
</script>
