import HomePage from '../pages/home.svelte';
import AboutPage from '../pages/about.svelte';
import ResourcesPage from '../pages/resources.svelte';
import NotFoundPage from '../pages/404.svelte';
import LoginPage from '../pages/login.svelte';
import AddProblemPage from '../pages/add-problem.svelte';
import MidStepPage from '../pages/mid-step.svelte';
import AddQuestionPage from '../pages/add-question.svelte';
import AddAnswerPage from '../pages/add-answer.svelte';
import PublicPage from '../pages/public.svelte';

const routes = [
  {
    path: '/',
    component: HomePage,
  },
  {
    path: '/about/',
    component: AboutPage,
  },
  {
    path: '/login/',
    component: LoginPage,
  },
  {
    path: '/resources/',
    component: ResourcesPage,
  },
  {
    path: '/public/',
    component: PublicPage,
  },
  {
    path: '/add-problem/',
    component: AddProblemPage,
  },
  {
    path: '/mid-step/',
    component: MidStepPage,
  },
  {
    path: '/add-question/:problemId',
    component: AddQuestionPage,
  },
  {
    path: '/add-answer/:questionId',
    component: AddAnswerPage,
  },
  {
    path: '(.*)',
    component: NotFoundPage,
  },
];

export default routes;
