<Page>
  <Navbar title="//get_unstuck" backLink="Back"/>

  <Card>
    <List form>
      <ListInput
          label="Step details. Answer your question."
          type="text"
          floatingLabel
          clearButton
          value={stepTitle}
          onInput={(e) => stepTitle = e.target.value}
      />
    </List>
    <List>
      <ListButton onClick={addStep} color="blue">Add Answer</ListButton>
    </List>
  </Card>

  <BlockTitle>
    <strong>{answers.length} Answers</strong>
  </BlockTitle>

    {#if answers.length}
      <Card>
          {#each answers as answer, i (answer.id)}
            <Answer answer="{answer}"/>
          {/each}
      </Card>

      <Block>
        <div><strong>Tip:</strong> Swipe the Answer for more actions</div>
      </Block>
    {/if}
</Page>

<script>
  import {
    f7,
    Navbar,
    List,
    ListButton,
    ListInput,
    Page,
    Card,
    CardContent,
    CardFooter,
    Button,
    Block,
    ListItem,
    BlockTitle,
    SwipeoutActions,
    SwipeoutButton,
  } from 'framework7-svelte'

  import Api from '../services/Api'
  import Answer from '../pages/components/Answer.svelte'

  export let f7router
  export let f7route
  let answers = []
  let stepTitle = ''

  const questionId = f7route.params.questionId

  async function addStep () {
    if (!stepTitle.length) {
      f7.dialog.alert('Todo Step cannot be empty.')
      return
    }

    await Api.createAnswer(questionId, stepTitle)
    f7router.navigate('/')
  }

  // initial data
  Api.getAnswers(questionId).then(resp => answers = resp)
</script>
