<List>
  <ListItem swipeout title="{value}" onSwipeoutDeleted="{deleteAnswer}">
    <SwipeoutActions right>
      <SwipeoutButton delete confirmText="Are you sure you want to delete this answer?">Delete</SwipeoutButton>
    </SwipeoutActions>
  </ListItem>
</List>

<script>
  import { List, ListItem, SwipeoutActions, SwipeoutButton } from 'framework7-svelte'
  import Api from '../../services/Api'

  export let answer

  const { id, value } = answer

  function deleteAnswer() {
    Api.deleteAnswer(id)
  }
</script>
