<Card class="problemCard">
  <CardFooter class="problemCard-headerFooter">
    <strong>{name}</strong>
  </CardFooter>

    {#if comments.length}
      <CardContent class="problemCard-card">
        <Link on:click="{toggleComments}">Toggle {comments.length} comment{comments.length === 1 ? '' : 's'}</Link>
      </CardContent>
    {/if}

    {#if showComments}
      <List class="problemCard-list">
          {#each comments as comment (comment.id)}
              {#if parseInt(userId) === parseInt(comment.user_id)}
                <Comment comment="{comment}" on:deleteComment={deleteComment}/>
              {:else}
                <ListItem>
                  <span slot="header">
                      {comment.value}
                  </span>
                </ListItem>
              {/if}
          {/each}
      </List>
    {/if}

  <Row>
    <Col width="80">
      <List noHairlines>
        <ListInput
            type="text"
            placeholder="Enter comment"
            clearButton
            value={commentValue}
            onInput={(e) => commentValue = e.target.value}
        />
      </List>
    </Col>
    <Col width="20">
      <Button large onClick={addComment}>Add</Button>
    </Col>
  </Row>
</Card>

<style>
  :global(.problemCard-card) {
    text-align : center;
  }
</style>

<script>
  import { f7, Card, CardHeader, CardContent, CardFooter, Toggle, List, Button, Popover, ListInput, ListItem, Icon, Link, Row, Col, SwipeoutActions, SwipeoutButton } from 'framework7-svelte'
  import Api from '../../services/Api'
  import Comment from '../components/Comment.svelte'

  export let problem

  let comments = []
  let showComments = false
  let commentValue = ''
  const userId = localStorage.getItem('userId')
  const { id, name, online, created_at, questions } = problem

  function toggleComments () {
    showComments = !showComments
  }

  async function addComment () {
    if (commentValue.length) {
      f7.dialog.preloader()

      await Api.createComment(id, commentValue)
      commentValue = ''

      f7.dialog.close()

      // initial data
      Api.getComments(id).then(resp => comments = resp)
    }
  }

  function deleteComment({ detail }) {
    let resp = comments.filter(comment => comment.id !== detail.id)
    comments = [...resp];
  }

  // initial data
  Api.getComments(id).then(resp => comments = resp)
</script>
