<Page name="home">
  <Toolbar tabbar position="bottom">
    <Link href="/">Home</Link>
    <Link href="/public/" tabLinkActive>Public</Link>
  </Toolbar>

  {#if problems}
    {#if problems.length}
      <BlockTitle><strong>Public problems</strong></BlockTitle>
        {#each problems as problem, i (problem.id)}
          <PublicProblem problem="{problem}" f7router="{f7router}" />
        {/each}
    {:else}
      <Block>
        <p><strong>Often you will have multiple questions in your mind about your problem, and can be difficult to focus.</strong></p>
        <p><strong>Having everything written down will help solve your focus, and given enough time, you will solve the problem.</strong></p>
        <br>
        <Button fill href="/add-problem/">Start by asking a question</Button>
      </Block>
    {/if}
  {/if}
</Page>

<script>
  import {
    f7,
    Navbar,
    NavLeft,
    NavTitle,
    NavTitleLarge,
    NavRight,
    Link,
    Toolbar,
    Block,
    BlockTitle,
    Page,
    List,
    Card,
    CardContent,
    CardFooter,
    Button,
    Fab,
    Icon,
  } from 'framework7-svelte'

  import Api from '../services/Api'
  import PublicProblem from '../pages/components/PublicProblem.svelte'

  export let f7router
  let problems = null
  const userId = localStorage.getItem('userId')

  if (!userId) {
    f7router.navigate('/login/')
  }

  // initial data
  Api.getPublicProblems().then(resp => problems = resp)
</script>
